___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
}

form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
  margin: 100px;
  padding: 50px;
  background-color: aliceblue;
  box-shadow: 1px 1px 2px gray;
}

input {
  padding: 5px;
  outline: none;
  border: 1px solid black;
}

input:valid {
  border: 1px solid green;
}

input:focus:invalid,
.highlight-invalid {
  border: 1px solid red;
}

span {
  font-size: 12px;
  padding: 3px;
  background-color: yellow;
  border: 1px solid red;
}

button {
  padding: 5px 10px;
}
